.modal {
  @include size(100%);

  display: none;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  padding-top: 200px;
  overflow: auto;
  background-color: rgba($blue-light, .9);

  &__content {
    position: relative;
    margin: auto;
    padding: 0;
    border: 1px solid var(--gray-30);
    border-radius: 25px 5px 25px 25px;
    background-color: var(--white);
    width: 75%;
    max-width: 900px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s
  }

  &__header {
    @include flex(flex-start, center);
    @include make-spacer(pl, 6);
    @include make-spacer(pr, 12);
    @include make-spacer(py, 3);

    position: relative;
    color: var(--gray-text);
    border-bottom: 1px solid var(--gray-30);

    input {
      @include size(100%, 28px);
      @include make-spacer(px, 3);

      background-color: var(--blue-light);
      border: 1px solid var(--gray-30);
      border-radius: 5px;
      color: var(--gray-30);
      font-weight: normal;

      &:focus {
        border: 1px solid var(--gray-30);;
        outline: none;
      }
    }

    .search__img {
      @include make-spacer(mr, 4);
    }

    .search__icon {
      @include make-spacer(mr, 4);
    }
  }

  &__body {
    @include flex(flex-start, flex-start);
    @include make-spacer(pt, 4);
    @include make-spacer(pb, 14);
    @include make-spacer(px, 6);

    img {
      @include make-spacer(mr, 4);
    }
  }

  &--post {
    .modal__header {
      position: relative;

      .header__img {
        @include size(70px, auto);

        position: absolute;
        top: -25px;
        left: -25px;
      }

      .search__img {
        @include make-spacer(ml, 7);
      }

    }
    .modal__body {
      @include flex(space-between, center);
      @include make-spacer(pb, 8);
      @include make-spacer(mb, 4);

      border-bottom: 1px solid var(--gray-30);

      .user {
        @include flex(flex-start, flex-end);

        &__name {
          @include font-size(20px);

          font-weight: var(--bold);
        }
      }

      .text {
        font-weight: var(--bold);
      }

      .btn-radio {
        @include flex(center, center);

        &:first-child {
          @include make-spacer(mr, 5);
        }

        .dropdown__trigger {
          color: var(--gray-text);
        }
      }
    }

    //.custom-control-label {
    //  &:before {
    //    left: -15px;
    //  }
    //}

    .custom-control-input {
      @include size(25px);

      margin-left: -3px;
    }

    .custom-control-label {
      @include make-spacer(ml, 2);

      font-family: 'DM Mono', monospace;
      font-weight: normal;
    }


    .custom-control-input:checked~.custom-control-label::before {
      border-color: var(--gray-30);
      background-color: var(--main);
      outline: none;
    }

    .custom-control-input:checked~.custom-control-label::after {
      background-image: none;
    }

    .custom-control-label::before {
      @include size(25px);

      top: 0;
      left: -35px;
      border-color: var(--gray-30);
      background-color: var(--blue-light);
      outline: none;

      &:active {
        outline: none;
      }
    }

    .custom-control-label::after {
      content: none;
    }

    .dropdown-toggle::after {
      border: none;
    }

    .category__block {
      //@include size(200px, auto);
    }

    .dropdown__list {
      left: 100px;
    }

    .modal__form {
      @include make-spacer(px, 6);
      @include make-spacer(pb, 6);

      textarea {
        @include size(100%, auto);
        @include make-spacer(px, 6);
        @include make-spacer(py, 4);

        background-color: var(--blue-light);
        border-color: var(--gray-30);
        color: var(--gray-text);
        resize: none;

        &:focus {
          outline: none;
        }
      }

      .custom-file {
        @include size(50%, 60px);

        cursor: pointer;
      }

      .custom-file-input {
        width: 100%;
        cursor: pointer;
      }

      .custom-file-label {
        @include flex($direction: column);

        height: 100%;
        border: none;
        background-color: transparent;
        cursor: pointer;

        span {
          @include font-size(12px);
        }

        &:after {
          content: '';
          background-color: transparent;
        }

        .file-img {
          @include size(25px, auto);
          @include make-spacer(ml, 2);
          @include make-spacer(mb, 3);
        }
      }
    }

    .modal__btn {
      @include flex(space-between, center);
      @include make-spacer(pt, 4);
      @include make-spacer(pb, 2);

      .btn-right {
        img {
          @include make-spacer(mr, 1);
        }
      }
    }
  }
}

@-webkit-keyframes animatetop {
  from {top:-300px; opacity:0}
  to {top:0; opacity:1}
}

@keyframes animatetop {
  from {top:-300px; opacity:0}
  to {top:0; opacity:1}
}

.btn-close,
.btn-close-post,
.btn-close-feed,
.btn-close-blog {
  @include font-size(26px);
  @include make-spacer(px, 1);


  position: absolute;
  right: 10px;
  color: var(--gray-30);
  font-weight: bold;
  border: 1px solid var(--gray-30);
  border-radius: 5px;
}

.btn-close:hover,
.btn-close:focus,
.btn-close-post:hover,
.btn-close-post:focus,
.btn-close-feed:hover,
.btn-close-feed:focus,
.btn-close-blog:hover,
.btn-close-blog:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
