.profile {
  &.profile-company {
    .user__img {
      @include size(50px);

      top: -40px;
      left: -20px;
      z-index: 1;

      img {
        border-radius: 10px;
      }
    }

    .company__img {
      @include size(125px);
      @include make-spacer(mr, 4);

      position: absolute;

      img {
        @include size(100%);

        border: 3px solid var(--main);
        border-radius: 15px;
        margin-top: -62.5px;
      }
    }
  }

  &__name--company {
    @include flex(flex-start, center);

    .user-name {
      @include flex(flex-start, center);
      @include make-spacer(mr, 10);

      .icon-admin {
        @include make-spacer(mr, 3);
      }

      .profile__data {
        @include flex(center, center, column);

        .name {
          @include font-size(15px);
          @include make-spacer(m, 0);
          @include make-spacer(mb, 1);
        }

        .specialty {
          @include font-size(12px);
        }
      }
    }
  }
}

