.content {

  &__block {
    @include make-spacer(my, 10);

    //box-shadow: 1px 0 5px var(--gray);
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    border-radius: 15px;

    &--invert {
      background-color: var(--blue-light);

      .card__item {
        background-color: var(--blue-light);
      }

      .card__comments {
        background-color: var(--white);
      }

      .card__main {
        border-bottom-color: var(--gray-30);
      }
    }
  }

  &__header {
    @include flex(space-between, center);
    @include size(100%, 80px);
    @include make-spacer(pr, 5);
    @include make-spacer(pl, 17);

    background-color: var(--blue-light);
    position: relative;
    border-radius: 15px 15px 0 0;
    border-bottom: 1px solid var(--gray-30);

    .icon-lg {
      @include size(70px, auto);

      position: absolute;
      top: -20px;
      left: -15px;
    }

    .icon-md {
      @include size(50px, auto);

      position: absolute;
      top: -7px;
      left: -11px;
    }

    .dots {
      @include size(28px);
    }

    .title {
      @include flex(flex-start, flex-end);

      .count {
        @include font-size(30px);
        @include make-spacer(ml, 2);

        font-weight: var(--bold);
        color: var(--red);
        text-transform: uppercase;
      }
    }
  }

  &__list {
    @include make-spacer(my, 10);
    @include make-spacer(pl, 13);
    @include make-spacer(pr, 4);
    @include make-spacer(py, 6);
  }
}
