.worker {
  //@include flex(space-between, flex-start, row, wrap);
  @include make-spacer(p, 4);
  @include make-spacer(pr, 12);
  @include make-spacer(pb, 0);

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: space-between;
  grid-column-gap: 48px;
  grid-row-gap: 70px;

  @include media-breakpoint-down(md) {
    grid-template-columns: repeat(3, 1fr);
  }

  @include media-breakpoint-down(sm) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include media-breakpoint-down(xs) {
    grid-template-columns: repeat(1, 1fr);
  }

  //gap: 58px;

  &__item {

    @include flex(flex-start, center, column);

    &.status-pending {

      .worker__location {
        display: none;
      }

      .worker__name {
        color: var(--gray-60);
      }

      .worker__img {
        filter: grayscale(1);
      }
    }
  }

  &__img {
    @include size(212px);

    img {
      @include size(100%);

      border-radius: 10px;
    }
  }

  &__name {
    @include font-size(25px);
    @include make-spacer(mt, 5);
    @include make-spacer(mb, 2);

    font-weight: var(--bold);
    text-align: center;
  }

  &__location {
    @include flex(center, center);
    @include make-spacer(mb, 4);

    span {
      @include font-size(12px);
      @include make-spacer(ml, 2);

      color: var(--gray-30);
    }
  }

  &__button {
    @include flex(center, center, column);

    .button {
      @include make-spacer(mb, 2);
    }
  }
}
