.section-main-content {
  @include make-spacer(mt, 15);

  .content {
    @include flex(space-between, flex-start);

    gap: 30px;

    &__block {
      position: relative;

      .edit {
        position: absolute;
        top: 20px;
        right: 16px;
        z-index: 100;
      }

    }
    &__list {
      @include make-spacer(p, 0);
      @include make-spacer(mt, 5);

      position: relative;

      .icon-lg {
        position: absolute;
        left: -10px;
        top: -80px;
        z-index: 10;
      }

      .icon-feed {
        left: -28px;
      }
    }
  }

  .card {
    &__main {
      @include flex($direction: column);
    }

    &__img {
      @include size(100%, auto);
      @include make-spacer(mb, 5);
      @include make-spacer(mr, 0);

      img {
        @include size(100%, auto);
      }
    }

    &__header {
      @include make-spacer(pt, 0);
    }

    &__text {
      font-family: 'Montserrat', sans-serif;
      font-weight: normal;

      a {
        font-family: inherit;
      }
    }
  }
}
