.section-industry_selection {

  .section__subtitle {
    @include make-spacer(mb, 5);

    color: var(--gray-10);
    font-weight: var(--bold);
  }

  .content__block {
    @include make-spacer(p, 10);
  }

  .industry {

    &__block {
      @include make-spacer(mb, 7);
    }

    &__list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 20px;
    }

    &__item {
      @include flex(flex-start, center);
      @include make-spacer(p, 5);
      @include make-spacer(px, 7);

      background-color: var(--gray-text);
      border-radius: 15px;
      color: var(--white);
    }

    &__icon {
      @include size(50px);
      @include make-spacer(mr, 4);

      border-radius: 0;
    }
  }

  .custom-control {
    @include make-spacer(mr, 3);
  }

  .custom-control-input {
    @include size(25px);

    margin-left: -3px;
  }

  .custom-control-label {
    @include make-spacer(ml, 2);

    font-family: 'DM Mono', monospace;
    font-weight: normal;
  }

  .custom-control-input:checked~.custom-control-label::before {
    border-color: var(--gray-30);
    background-color: var(--main);
    outline: none;
  }

  .custom-control-input:checked~.custom-control-label::after {
    background-image: none;
  }

  .custom-control-label::before {
    @include size(25px);

    top: 0;
    left: -35px;
    border-color: var(--gray-30);
    background-color: var(--blue-light);
    border-radius: 50%;
    outline: none;

    &:active {
      outline: none;
    }
  }

  .section__btn {
    text-align: center;

    .button {
      i {
        @include make-spacer(mr, 2);
      }
    }
  }
}
