.button {
  @include flex(center, center, $display: inline-flex);
  @include make-spacer(m, 0);
  @include make-spacer(py, 4);
  @include make-spacer(px, 2);
  @include make-spacer(px, 4, sm);
  @include font-size(14px, 16px);
  @include transition(#{background-color, box-shadow, color});
  @include size(170px, 34px);

  position: relative;
  outline: none;
  background-color: var(--yellow);
  border-radius: 7px;
  cursor: pointer;
  vertical-align: middle;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
  color: var(--gray-text);
  user-select: none;
  -webkit-appearance: none; // stylelint-disable-line
  -webkit-tap-highlight-color: transparent;
  text-align: center;
  font-family: var(--headings-font-family);
  font-weight: var(--bold);

  &--fluid {
    width: 100%;
  }

  &--main {
    background-color: var(--main);
    border: 1px solid var(--main);
  }

  &--secondary {
    background-color: var(--blue-light);
    border: 1px solid var(--gray-30);
  }

  &--gray {
    background-color: var(--gray-100);
    border: 1px solid var(--gray-100);
    color: var(--main);
  }

  &--link {
    @include size(auto);

    background: transparent;
    border: none;
    color: var(--white);

    &:hover {
      color: var(--yellow);
    }
  }

  &--md {
    @include size(124px, 40px);
  }

  i {
    @include make-spacer(ml, 2);
  }

  &:hover {
    opacity: 0.8;
  }
}
